@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff"); }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-style: normal;
  font-weight: 400;
  color: #533738;
  background-color: #efe6e4;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.container {
  margin: 0 auto;
  padding: 0 50px;
  max-width: 1300px;
  width: 100%; }

.main-header {
  padding-top: 24px;
  padding-bottom: 168px;
  background: url("../../img/main-intro.jpg") 0 0 no-repeat;
  background-size: cover; }

.main-header__wrap {
  display: grid;
  grid-template-columns: 130px 1fr 145px;
  column-gap: 35px;
  padding-bottom: 78px; }

.main-header__logo {
  position: relative;
  padding-top: 10px;
  padding-left: 25px; }
  .main-header__logo::before {
    content: "";
    position: absolute;
    top: 12px;
    left: 0;
    width: 50px;
    height: 50px;
    background: url("../../img/lamp.png") no-repeat; }

.main-header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.main-header__nav-link {
  display: inline-flex;
  padding: 15px 13px;
  font-size: 16px;
  line-height: 20px;
  color: inherit; }

.main-header__tel {
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 16px;
  line-height: 20px;
  color: inherit; }

.main-header__intro {
  width: 555px;
  padding-left: 85px; }

.main-header__title {
  margin: 0;
  padding: 0;
  font-size: 50px;
  line-height: 62px;
  font-weight: 400;
  padding-bottom: 20px; }

.main-header__text {
  margin: 0;
  padding: 0;
  padding-right: 30px;
  padding-bottom: 25px; }

.main-header__bid {
  display: inline-flex;
  padding: 25px 65px;
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  text-align: center;
  color: inherit;
  background-color: #cc8b86; }

.main-footer {
  display: grid;
  grid-template-columns: 183px 1fr 165px;
  column-gap: 20px;
  padding-top: 33px;
  padding-bottom: 41px; }

.main-footer__tel {
  color: inherit; }

.main-footer__entity {
  margin: 0;
  padding: 0; }

.main-footer__adress {
  margin: 0;
  padding: 0; }

.about {
  padding: 65px 140px 21px 133px; }

.about__title {
  margin: 0;
  padding: 0;
  font-size: 40px;
  line-height: 50px;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase; }

.about__wrap {
  padding-top: 40px;
  display: grid;
  grid-template-columns: 444px 1fr;
  column-gap: 64px; }

.about__photo {
  width: 445px;
  height: 465px;
  background: url("../../img/about.jpg") 0 0 no-repeat; }

.about__text {
  margin: 0;
  padding: 0; }

.stats {
  padding-top: 87px;
  padding-bottom: 81px;
  background-color: #8d7370; }

.stats__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px 193px; }

.stats__item {
  text-align: center; }

.stats__number {
  font-size: 50px;
  line-height: 62px;
  color: #ffffff; }

.stats__text {
  margin: 0;
  padding: 0;
  padding-top: 6px;
  font-size: 30px;
  line-height: 37px;
  text-transform: uppercase;
  color: #ffffff; }

.works {
  padding: 65px 50px; }

.works__title {
  margin: 0;
  padding: 0;
  padding-bottom: 40px;
  font-size: 40px;
  line-height: 50px;
  font-weight: 400;
  text-transform: uppercase; }

.works__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 21px 20px;
  padding-bottom: 40px; }

.works__item {
  position: relative; }
  .works__item--flat:hover > .works__info--small {
    display: block; }
  .works__item--kitchen:hover > .works__info--large {
    display: block; }
  .works__item--lounge:hover > .works__info--large {
    display: block; }
  .works__item--bathroom:hover > .works__info--small {
    display: block; }

.works__img {
  height: 451px;
  object-fit: cover; }

.works__info {
  display: none;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 451px;
  padding: 35px;
  padding-top: 86px;
  background-color: rgba(100, 72, 69, 0.8); }
  .works__info--small {
    width: 488px; }
  .works__info--large {
    width: 692px; }

.works__info-title {
  margin: 0;
  padding: 0;
  padding: 0 54px 19px;
  font-size: 30px;
  line-height: 37px;
  font-weight: 400;
  text-align: center;
  color: #ffffff; }

.works__info-text {
  margin: 0;
  padding: 0;
  color: #ffffff; }

.works__more {
  display: block;
  padding-right: 70px;
  font-weight: 500;
  font-size: 30px;
  line-height: 37px;
  text-align: center;
  color: inherit; }
